.myButton {
    margin: 5px;
    padding: 10px 20px;
    background-color: rgb(183, 183, 255);
    border: none;
    border-radius: 10px;
    cursor: pointer;
    /* box-shadow: 10px 10px 16px rgb(23, 23, 98), -10px -10px -10px rgb(17, 17, 55); */
    transition: all 0.2s ease-in-out;
  }
  
  .myButton:hover {
    background-color: rgb(223, 223, 252);
  }
  
  .myButton:active {
    background-color: rgb(29, 29, 94);
    color: white;
    /* box-shadow: inset 3px 3px 6px rgb(29, 29, 94), inset -3px -3px -6px rgb(26, 26, 82); */
  }