.genderForm {
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  .genderForm > input {
    margin-bottom: 3px;
  }
  
  .genderForm > button {
    border: none;
    border-radius: 2px;
    background-color: rgb(255, 208, 255);
  }
  
  .errorContainer {
    top: -10px;
    left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 148px;
    height: 18px;
    background-color: rgba(255, 217, 0, 0.74);
  }
  
  .errorContainer > span {
    font-size: 8px;
    color: red;
  }